.commonNavBar {
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: white;
}

.displayNone {
    display: none;
}


/*Top NavBar*/
.topNavBar {
    background-color: white;
    height: 76px;
    transition: height 0.3s linear,
    opacity 0.8s linear;
}

.hide {
    /*display: none;*/
    opacity: 0;
    padding-top: 0;
    padding-bottom: 0;
    height: 0;
    transition: height 0.4s ease-out,
    opacity 0.3s linear;
}


@media (max-width: 991px) {

    .topNavBar{
        height: auto;
    }

    .hide {
        display: none;
    }

    .searchForm {
        margin-top: 20px;
    }

    .geo {
        margin-top: 20px;
        margin-bottom: 20px;
    }
}


/* Bottom navBar */

.bottomNavBar {
    background-color: rgb(50, 148, 63);
}

.container {
}

.allCategories {
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

.bottomNavBar li a:hover {
    background-color: #111;
}

@media (max-width: 991px) {
    .allCategories {
        padding-bottom: 0;
        padding-top: 8px;
    }
}

